* {
  box-sizing: border-box;
}

body {
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

body > p {
  display: inline-block;
padding: 10px;
background-color: rgba(162, 48, 48, 0.308);
border-radius: 10px;
}

img {
  display: block;
  max-width: 100%;
}

form {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
padding: 5px;
margin: 15px;
}

label {
padding: 5px;
margin: 5px;
}

.button {
display: block;
width: 200px;
height: 50px;
border-radius: 5px;

}


.wrapper_input{
  display: inline-block;
  padding: 10px;
  border-radius: 15px;
  background-color: rgba(174, 180, 180, 0.183);
}
.input{
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  border-radius: 5px;

  background-color: rgba(255, 235, 205, 0.084);

  font-size: 25px;
}

.year_time{
padding: 20px;
margin-left: auto;
margin-right: auto;
background-color: rgba(235, 203, 182, 0.441);
}

.year_time > p{
display: block;
margin-left: auto;
margin-right: auto;
}

.timer{
  display: inline-flex;
  justify-content: space-around;
  margin-left: auto;
  margin-right: auto;
padding: 20px;
max-width: 700px;
border-radius: 10px;
background-color: rgba(131, 185, 232, 0.385);
}

.field{
margin: 5px;
font-style:italic;
font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif
}